import React from 'react'

import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'

import Intro from "../pages/Intro.js";
import Home from "../pages/Home.js";
import Projects from "../pages/Projects.js";
import Studio from "../pages/Studio.js";
import Project from "../pages/Project.js";
import Contact from "../pages/Contact.js";
import NotFound from "../pages/404.js";

import HomeButton from "../components/HomeButton"

console.log(process.env.PUBLIC_URL)

const Main = props =>
    <Router>
        <HomeButton />
        <Switch>
            <Route exact path={`${process.env.PUBLIC_URL}/`} component={ Intro } />
            <Route path={`${process.env.PUBLIC_URL}/home`} component={ Home } />
            <Route path={`${process.env.PUBLIC_URL}/studio`} component={ Studio } />
            <Route path={`${process.env.PUBLIC_URL}/projects`} component={ Projects } />
            <Route exact path={`${process.env.PUBLIC_URL}/project`}><Redirect to={`${process.env.PUBLIC_URL}/projects`} /></Route>
            <Route path={`${process.env.PUBLIC_URL}/project/:slug`} component={ Project } />
            <Route path={`${process.env.PUBLIC_URL}/contact`} component={ Contact } />
            <Route component={ NotFound } />
        </Switch>
    </Router>

export default Main